export function getParentData(parentName = '') {
  // createdparent
  if (!this.parent) this.parent = {};
  // (u-radiou-radio-groupthis)
  // this(u-radiothis)parentData
  // this.parent.xxx
  // u-radio-groupdata
  this.parent = $parent.call(this, parentName);
  if (this.parent.children) {
    // childrenchildren
    this.parent.children.indexOf(this) === -1 && this.parent.children.push(this);
  }
  if (this.parent && this.parentData) {
    // parentDataparentparentData
    Object.keys(this.parentData).map((key) => {
      this.parentData[key] = this.parent[key];
    });
  }
}


/**
* @description provide/inject
   this.$parentH5H5this.$parent.$parent.xxx
   undefined()$parentundefinedname
   (undefined)$parent
*  @param {string|undefined} name 
*/
function $parent(name = undefined) {
  let parent = this.$parent;
  // whileH5
  while (parent) {
    // 
    if (parent.$options && parent.$options.name !== name) {
      // name
      parent = parent.$parent;
    } else {
      return parent;
    }
  }
  return false;
}


/**
 * @description u-formu-form
 * @param {*} instance
 * @param {*} event
 */
export function formValidate(instance, event) {
  const formItem = $parent.call(instance, 'PressFormItem');
  const form = $parent.call(instance, 'PressForm');
  // inputtextareau-form-itemu-formformvalidate
  // form-itemprosform
  if (formItem && form) {
    form.validateField(formItem.prop, () => {}, event);
  }
}
